import Header from "@/components/common/Header";
import QRcode from "@/components/common/QRcode";

export default {
  name: "",
  props: [""],
  data() {
    return {
      TitleObjData: {
        titleContent: "通证",
        showLeftBack: true,
        showRightMore: false
      },
      userInfo: null
    };
  },

  components: {
    Header,
    QRcode
  },

  computed: {
    conStyle() {
      return { height: document.documentElement.clientHeight - 45 + "px" };
    }
  },
  created() {
    this.getUserInfo();
  },
  beforeMount() {},

  mounted() {},

  methods: {
    getUserInfo() {
      this.userInfo = this.GLOBAL.getSession("userLoginInfo");
    }
  },

  watch: {}
};



